import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';

const FilmItem = ({ id, title, posterPath, voteAverage, overview }) => {
  const addToFavorites = () => {};
  const addToWatched = () => {};
  return (
    <div className="filmWrapper">
      <NavLink to={`/${title}/${id}`}>
        <img
          src={
            posterPath
              ? `https://image.tmdb.org/t/p/w200/${posterPath}`
              : 'https://www.viser.edu.rs/images/no_photo.jpeg'
          }
          alt="film poster"
        />
      </NavLink>{' '}
      <div>
        <h3> {title} </h3> <p>{voteAverage || 'no votes'}</p>
        <p> {overview || 'no overview'} </p>{' '}
        <button type="button" onClick={addToFavorites}>
          {' '}
          Add to fav{' '}
        </button>{' '}
        <button type="button" onClick={addToWatched}>
          {' '}
          Add to watched{' '}
        </button>{' '}
      </div>{' '}
      <button type="button">
        <NavLink to={`/${title}/${id}`}> Show more </NavLink>{' '}
      </button>{' '}
    </div>
  );
};
FilmItem.propTypes = {
  id: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  posterPath: PropTypes.string.isRequired,
  voteAverage: PropTypes.number.isRequired,
  overview: PropTypes.string.isRequired,
};

export default FilmItem;
