import axios from 'axios';
import {
  filmsLoadingActionCreator,
  filmsSuccessActionCreator,
  filmsErrorActionCreator,
} from './filmsSearchActions';

const API_KEY = 'bcf1e9109b9838d979a3eda6f6128d7e';

const fetchFilms = query => dispatch => {
  dispatch(filmsLoadingActionCreator());
  axios
    .get(
      `https://api.themoviedb.org/3/search/movie?api_key=${API_KEY}&query=${query}&include_adult=false`,
    )
    .then(response =>
      dispatch(filmsSuccessActionCreator(response.data.results)),
    )
    .catch(error => dispatch(filmsErrorActionCreator(error)));
};
export default fetchFilms;
